
"use client";
import { useToast } from "@/hooks/use-toast";
import React, { useState, useEffect, useRef } from "react";
import { MapPin, University, User, School2Icon, Calendar, Flame, Clock3, School, DollarSign, Sparkles, Rocket, Stars, ChevronLeft, ChevronRight } from "lucide-react";
import { motion, AnimatePresence } from 'framer-motion';
import EventCard from "@/components/ui/EventCard";
import { events } from '@/constants/events';
import { EventCategory } from "@/types/type";
import Link from "next/link";

const filters = [
    { label: "All", icon: School2Icon },
    { label: "Weekdays", icon: Calendar },
    { label: "Popular", icon: Flame },
    { label: "Latest", icon: Clock3 },
    { label: "University", icon: School },
    { label: "Price", icon: DollarSign },
];

const FloatingBlobs = () => (
    <>
        {[...Array(5)].map((_, i) => (
            <motion.div
                key={i}
                initial={{ scale: 0, rotate: 0 }}
                animate={{
                    scale: [0.8, 1.2, 0.8],
                    rotate: [0, 180, 360],
                    x: Math.sin((i * Math.PI) / 2.5) * 100,
                    y: Math.cos((i * Math.PI) / 2.5) * 100,
                }}
                transition={{
                    duration: 10 + i * 2,
                    repeat: Infinity,
                    ease: "easeInOut",
                }}
                className={`absolute w-${40 + i * 20} h-${40 + i * 20} opacity-10 blur-xl
                bg-gradient-to-r ${i % 2 ? 'from-pink-400 to-purple-400' : 'from-cyan-400 to-indigo-400'}
                rounded-full`}
            />
        ))}
    </>
);

const AllEventsPage = ({ className }: React.HTMLAttributes<HTMLDivElement>) => {
    const { toast } = useToast();
    const [event, setEvents] = useState<EventCategory[]>([])
    const [activeFilter, setActiveFilter] = useState<'all' | 'weekdays' | 'popular' | 'latest' | 'university' | 'price'>('all');
    const [isLoading, setIsLoading] = useState(true);
    // حالة Pagination الجديدة
    const [currentPage, setCurrentPage] = useState(1);
    const [eventsPerPage] = useState(6); // عدد الأحداث في كل صفحة
    const filteredEvents = events.filter(event => {
        if (activeFilter === 'all') return true;
        if (activeFilter === 'weekdays') return ['1', '2', '6'].includes(event.id);
        if (activeFilter === 'popular') return ['1', '4'].includes(event.id);
        if (activeFilter === 'latest') return ['3', '5'].includes(event.id);
        if (activeFilter === 'university') return ['4', '5'].includes(event.id);
        if (activeFilter === 'price') return ['1'].includes(event.id);
        return true;
    });

    // حساب الأحداث للصفحة الحالية
    const indexOfLastEvent = currentPage * eventsPerPage;
    const indexOfFirstEvent = indexOfLastEvent - eventsPerPage;
    const currentEvents = filteredEvents.slice(indexOfFirstEvent, indexOfLastEvent);
    const totalPages = Math.ceil(filteredEvents.length / eventsPerPage);

    // تغيير الصفحة
    const paginate = (pageNumber: number) => setCurrentPage(pageNumber);


    useEffect(() => {
        if (isLoading) return;

        const activeIndex = filters.findIndex(f => f.label.toLowerCase() === activeFilter);

        const fetchEvents = async () => {
            try {
                setIsLoading(true);
                const filterType =
                    activeFilter === "all"
                        ? ""
                        : activeFilter === "upcoming"
                            ? "upcoming"
                            : activeFilter === "past"
                                ? "past"
                                : "";
                const url = `${process.env.NEXT_PUBLIC_API_URL}/events?type=${filterType}&page=${currentPage}&perPage=${eventsPerPage}`;
                const response = await fetch(url, {
                    method: "GET",
                })
                console.log("status : ", response.status)
                const data = await response.json();

                setEvents(data);
            } catch (err: any) {
                toast({
                    variant: "destructive",
                    title: "Error!",
                    description: err.message || "An unexpected error occurred.",
                });
            } finally {
                setIsLoading(false);
            }
        }
        fetchEvents();
    }, [activeFilter]);

    // إعادة تعيين الصفحة إلى 1 عند تغيير الفلتر
    useEffect(() => {
        setCurrentPage(1);
    }, [activeFilter]);
    return (
        <main className="bg-white min-h-screen relative overflow-hidden">
            {/* <Link href="events/create"><Button>إنشاء فعالية جديدة</Button></Link> */}

            <div className="max-w-6xl mx-auto px-4 py-10 relative">
                <div className="flex flex-wrap justify-center gap-4 mb-14 relative">
                    {filters.map(({ label, icon: Icon }, index) => {
                        const filterKey = label.toLowerCase() as typeof activeFilter;
                        return (
                            <motion.button
                                key={label}
                                whileHover={{
                                    scale: 1.05,
                                    background: 'linear-gradient(45deg, #9333ea, #4f46e5)'
                                }}
                                whileTap={{ scale: 0.95 }}
                                onClick={() => setActiveFilter(filterKey)}
                                className={`relative flex items-center gap-3 px-6 py-3 rounded-full
                                            border-2 backdrop-blur-lg transition-all duration-300
                                            ${activeFilter === filterKey
                                        ? 'border-purple-500/30 bg-purple-600/20 text-white'
                                        : 'border-white/10 bg-black/95 text-purple-100 hover:bg-purple-500/20'}`}
                                style={{
                                    boxShadow: activeFilter === filterKey
                                        ? '0 4px 24px -2px rgba(165, 180, 252, 0.3)'
                                        : 'none'
                                }}
                            >
                                <Icon size={20} className="text-cyan-400" />
                                <span className="text-sm font-medium tracking-wide">{label}</span>
                                {activeFilter === filterKey && (
                                    <motion.div
                                        className="absolute -bottom-2 h-1 bg-cyan-400 rounded-full"
                                        initial={{ scaleX: 0 }}
                                        animate={{ scaleX: 1 }}
                                        transition={{ type: 'spring', stiffness: 200 }}
                                    />
                                )}
                            </motion.button>
                        );
                    })}
                </div>
                <motion.div
                    className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8"
                    initial="hidden"
                    animate="visible"
                    variants={{
                        hidden: { opacity: 0 },
                        visible: {
                            opacity: 1,
                            transition: {
                                staggerChildren: 0.1,
                                delayChildren: 0.2
                            }
                        }
                    }}
                >
                    <AnimatePresence>
                        {currentEvents.map((event, index) => {
                            return (
                                <motion.div
                                    key={event.id}
                                    variants={{
                                        hidden: { opacity: 0, y: 20 },
                                        visible: { opacity: 1, y: 0 }
                                    }}
                                    whileHover={{
                                        y: -5,
                                        transition: { type: 'spring', stiffness: 300 }
                                    }}
                                    className="relative group"
                                >
                                    <Link href={`events/${event.id}`}>
                                        <EventCard
                                            event={event}
                                            className="transition-all duration-300 group-hover:shadow-xl
                                        group-hover:shadow-purple-500/20 group-hover:border-cyan-400/30"
                                        >
                                            {/* محتوى البطاقة */}
                                            <div className="p-4" >
                                                <div className='flex justify-between items-center'>
                                                    <div>
                                                        <h3 className="text-lg font-bold text-gray-800 mb-1" >Title : {event.title} </h3>
                                                        < p className="text-sm text-gray-500 mb-3" > By {event.department} </p>
                                                    </div>
                                                    <div className="flex flex-col items-center" >
                                                        <span className="text-2xl font-bold text-gray-800" > {event.time} </span>
                                                        < span className="text-sm font-medium text-purple-500 uppercase" > {event.date} </span>
                                                    </div>
                                                </div>
                                                < div className="space-y-2 mb-4" >
                                                    < span className="text-xs text-gray-700" >{event.description}</span>
                                                </div>
                                                < div className="flex space-y-2" >
                                                    <User className='text-xs text-gray-500 pe-2' />
                                                    <span>registered :{ }</span>
                                                </div>
                                                < div className="flex space-y-2" >
                                                    <University className='text-xs text-gray-500 pe-2' />
                                                    <span>capacity : {event.capacity}</span>
                                                </div>

                                                {
                                                    event.prices && (
                                                        <div className="flex gap-2 mb-4" >
                                                            <span className="bg-gray-100 text-gray-800 px-2 py-1 rounded text-sm" >
                                                                {event.prices}
                                                            </span>
                                                        </div>
                                                    )
                                                }
                                                <div className='flex items-center justify-between'>
                                                    <p className="text-xs text-gray-500" > {event.location} </p>
                                                    <MapPin className='text-xs text-gray-500' />
                                                </div>
                                            </div>
                                        </EventCard>
                                    </Link>
                                    <motion.div
                                        initial={{ scale: 0 }}
                                        animate={{ scale: 1 }}
                                        className="absolute top-2 right-2 p-2 bg-purple-600/80 rounded-full
                                    backdrop-blur-sm"
                                    >
                                        <Rocket size={18} className="text-cyan-400" />
                                    </motion.div>
                                </motion.div>
                            );
                        })}
                    </AnimatePresence>
                </motion.div>

                {/* Pagination Controls */}
                {
                    filteredEvents.length > eventsPerPage && (
                        <div className="flex justify-center mt-12">
                            <nav className="flex items-center gap-2">
                                <button
                                    onClick={() => paginate(Math.max(1, currentPage - 1))}
                                    disabled={currentPage === 1}
                                    className={`p-2 rounded-full ${currentPage === 1 ? 'opacity-50 cursor-not-allowed' : 'hover:bg-purple-500/20'}`}
                                >
                                    <ChevronLeft className="text-purple-400" />
                                </button>

                                {Array.from({ length: totalPages }).map((_, index) => (
                                    <button
                                        key={index}
                                        onClick={() => paginate(index + 1)}
                                        className={`w-10 h-10 rounded-full flex items-center justify-center
                                                ${currentPage === index + 1
                                                ? 'bg-purple-600 text-white'
                                                : 'text-purple-400 hover:bg-purple-500/20'
                                            } `}
                                    >
                                        {index + 1}
                                    </button>
                                ))
                                }

                                <button
                                    onClick={() => paginate(Math.min(totalPages, currentPage + 1))}
                                    disabled={currentPage === totalPages}
                                    className={`p-2 rounded-full ${currentPage === totalPages ? 'opacity-50 cursor-not-allowed' : 'hover:bg-purple-500/20'} `}
                                >
                                    <ChevronRight className="text-purple-400" />
                                </button >
                            </nav >
                        </div >
                    )}
            </div >
        </main >
    );
}
export default AllEventsPage;